/**********************************************************************************
** default
***********************************************************************************/
input[type=button], input[type=submit], input[type=reset] {width: auto;}
input[type=button][class*='btn-'], input[type=submit][class*='btn-'], input[type=reset][class*='btn-'] {line-height: 1;}
[class*='btn-'] .text{position: relative; }
[class*='btn-'] [class*='bic-'] + .text{margin-left: 6px;}
.btn-lg [class*='bic-']{transform: translateY(-1px);}
.btn .text + [class*='bic-']{margin-left: 3px;}
.btn-sm [class*='bic-'] + .text{margin-left: 4px;}
.btn-sm .text + [class*='bic-']{margin-left: 2px;}

/**********************************************************************************
** common button
***********************************************************************************/
a.btn, .btn, .btn-lg, a.btn-lg, .btn-sm, a.btn-sm, .btn-xs, a.btn-xs{display: inline-flex; align-items: center; justify-content: center; border: 1px solid #00b495; vertical-align: top; text-align: center; word-break: keep-all; word-wrap: break-word; transition: background-color 0.23s, color 0.23s, border-color 0.23s; -webkit-transition: background-color 0.23s, color 0.23s, border-color 0.23s;}

/* 크기 */
a.btn-lg, .btn-lg, .ui-widget a.btn-lg, .ui-widget .btn-lg{min-width: 163px; height: 50px; padding: 0 11px 0 10px; font-size: 14px; letter-spacing: -0.35px; line-height: 20px;}
a.btn-lg .text, .btn-lg .text, .ui-widget a.btn-lg .text, .ui-widget .btn-lg .text,
a.btn-lg .text em, .btn-lg .text em, .ui-widget a.btn-lg .text em, .ui-widget .btn-lg .text em {font-weight: 700;}
a.btn, .btn, .ui-widget a.btn, .ui-widget .btn{min-width: 120px; height: 40px; padding: 0 15px 0 14px; font-size: 13px; letter-spacing: -0.325px; line-height: 19px;}
a.btn .text, .btn .text, .ui-widget a.btn .text, .ui-widget .btn .text{font-weight: 500;}
a.btn-sm, .btn-sm, .ui-widget a.btn-sm, .ui-widget .btn-sm{min-width: 98px; height: 32px; padding: 0 10px 0 9px; font-size: 13px; line-height: 19px; letter-spacing: -0.163px;}
a.btn-sm  .text, .btn-sm  .text, .ui-widget a.btn-sm  .text, .ui-widget .btn-sm .text{font-weight: 300;}
a.btn-xs, .btn-xs, .ui-widget a.btn-xs, .ui-widget .btn-xs{min-width: 50px; height: 27px; padding: 0 10px 0 9px; font-size: 12px; font-weight: 300; letter-spacing: -0.3px; line-height: 18px;}

/* 컬러 */
a.btn-spot, .btn-spot{border-color: #00b495; background-color: #00b495; color: #fff;}
a.btn-spot .text, .btn-spot .text { color: #fff; }
a.btn-spot:active, .btn-spot:active, a.btn-spot:focus, .btn-spot:focus{border-color: #00b495; background-color: #00b495;}
a.btn-spot.disabled:active, .btn-spot[disabled]:active, a.btn-spot.disabled:focus, .btn-spot[disabled]:focus,
.btn-spot[readonly]:active , .btn-spot[readonly]:focus {border-color: #00b495; background-color: #00b495;}
a.btn-spot.disabled, .btn-spot[disabled], .btn-spot[readonly]{opacity: .3;}

a.btn-lnspot, .btn-lnspot{border-color: #00b495; background-color: #fff; }
a.btn-lnspot .text, .btn-lnspot .text { color: #00b495; }
a.btn-lnspot:active, .btn-lnspot:active, a.btn-lnspot:focus, .btn-lnspot:focus{background-color: #f7f7f7;}
a.btn-lnspot.disabled:active, .btn-lnspot[disabled]:active, a.btn-lnspot.disabled:focus, .btn-lnspot[disabled]:focus,
.btn-lnspot[readonly]:active , .btn-lnspot[readonly]:focus {border-color: #00b495; background-color: #fff; color: #00b495;}
a.btn-lnspot.disabled, .btn-lnspot[disabled], .btn-lnspot[readonly]{opacity: .3;}

a.btn-lngray, .btn-lngray{border-color: #ccc; background-color: #fff; }
a.btn-lngray .text, .btn-lngray .text { color: #000; }
a.btn-lngray:active, .btn-lngray:active, a.btn-lngray:focus, .btn-lngray:focus{background-color: #f7f7f7;}
a.btn-lngray.disabled:active, .btn-lngray[disabled]:active, a.btn-lngray.disabled:focus, .btn-lngray[disabled]:focus,
.btn-lngray[readonly]:active , .btn-lngray[readonly]:focus {border-color: #ccc; background-color: #fff; color: #000;}
a.btn-lngray.disabled, .btn-lngray[disabled], .btn-lngray[readonly]{opacity: .3;}

/*a.btn-lngray-light, .btn-lngray-light{border-color: #ddd; background-color: #fff; color: #000;}
a.btn-lngray-light:active, .btn-lngray-light:active, a.btn-lngray-light:focus, .btn-lngray-light:focus{background-color: #f7f7f7;}
a.btn-lngray-light.disabled:active, .btn-lngray-light[disabled]:active, a.btn-lngray-light.disabled:focus, .btn-lngray-light[disabled]:focus,
.btn-lngray-light[readonly]:active , .btn-lngray-light[readonly]:focus {border-color: #ddd; background-color: #fff; color: #000;}
a.btn-lngray-light.disabled, .btn-lngray-light[disabled], .btn-lngray-light[readonly]{opacity: .3;}*/

a.btn-black, .btn-black{border-color: #333; background-color: #333;}
a.btn-black .text, .btn-black .text { color: #fff; }
a.btn-black:active, .btn-black:active, a.btn-black:focus, .btn-black:focus{background-color: #000;}
a.btn-black.disabled:active, .btn-black[disabled]:active, a.btn-black.disabled:focus, .btn-black[disabled]:focus,
.btn-black[readonly]:active , .btn-black[readonly]:focus {border-color: #333; background-color: #333; color: #fff;}
a.btn-black.disabled, .btn-black[disabled], .btn-black[readonly]{opacity: .3;}

/* btn-lg btn-lngray 컬러만 font-weight: 300 */
a.btn-lg.btn-lngray .text, .btn-lg.btn-lngray .text, .ui-widget a.btn-lg.btn-lngray .text, .ui-widget .btn-lg.btn-lngray .text{font-weight: 300; word-break: keep-all; }

/**********************************************************************************
** 버튼정렬
***********************************************************************************/
.btn-wrap{display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; font-size: 0; text-align: center;}
.btn-wrap:not(.auto) [class*=btn]{flex: 1; min-width: auto;}
.btn-wrap [class*=btn] + [class*=btn]{margin-left: 4px;}

.btn-wrap.auto {justify-content: center;}
.btn-wrap.auto [class*=btn]{flex: 0 1 auto; }
.btn-wrap.auto .btn {}
.btn-wrap.auto .btn {}

.btn-wrap.full{display: block;}
.btn-wrap.full [class*=btn]{width: 100%; min-width: auto;}
.btn-wrap.full [class*=btn] + [class*=btn]{margin: 10px 0 0 0;}

.btn-wrap.page-bottom {margin-top: 30px;}
.btn-wrap.page-bottom ~ .noti-desc {margin-top: 10px;}

.container-wrapper .container-body .contbox.sm .contbox-body .contbox-body-inner .btn-wrap { width: 100%; }
.btn-wrap.type-guide ~ .ord-state-box-wrap { margin-top: 23px; margin-bottom: 4px; }

.btn-wrap .arr {display: inline-block; margin-right: 4px; width: 10px; height: 50px; background: url(../images/ui/ui_bic_link_black_text_arr.png) 0 / 100% no-repeat; vertical-align: top;}

/**********************************************************************************
** icon button
***********************************************************************************/
.btn-lg.btn-next-step .bic-next-step{margin-left: 10px;}
.btn-lg.btn-next-step .text{padding-left: 9px;}
.btn-prev-step{display: inline-flex; align-items: center; justify-content: center; height: 50px; padding: 0 15px 0 14px; border: 1px solid transparent; font-size: 14px; letter-spacing: -0.35px; line-height: 20px;}
.btn-prev-step .text{font-weight: 700; margin-right: 9px;}
.btn-prev-step .bic-prev-step + .text{margin-left: 10px;}


/**********************************************************************************
** etc
***********************************************************************************/
a.link-text, .link-text{display: inline-block; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #00b495; vertical-align: top;}
a.link-text:after, .link-text:after{content: ''; display: inline-block; width: 8px; height: 8px; margin: 5px 0 0 3px; background: url(../images/ui/ui_bic_link_text_arr.png) 0 / 100% no-repeat; vertical-align: top;}
a.link-text.sm, .link-text.sm{font-size: 11px; line-height: 17px; letter-spacing: -0.28px;}
a.link-text.sm:after, .link-text.sm:after{margin-top: 4px;}

a.link-black-text, .link-black-text{display: inline-block; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #000; vertical-align: top;}
a.link-black-text:after, .link-black-text:after{content: ''; display: inline-block; width: 8px; height: 8px; margin: 5px 0 0 4px; background: url(../images/ui/ui_bic_link_black_text_arr.png) 0 / 100% no-repeat; vertical-align: top;}

a.link-black-text.sm, .link-black-text.sm { font-size: 12px; line-height: 18px; letter-spacing: -0.3px; color: #000; }
a.link-black-text.sm:after, .link-black-text.sm:after { margin-left: 0; margin-top: 5px; }

a.link-gray-text, .link-gray-text{display: inline-block; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #666; vertical-align: top;}
a.link-gray-text:after, .link-gray-text:after{content: ''; display: inline-block; width: 8px; height: 8px; margin: 5px 0 0 4px; background: url(../images/ui/ui_bic_link_gray_text_arr.png) 0 / 100% no-repeat; vertical-align: top;}

.link-line-text, a.link-line-text{display: inline-block; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #666; vertical-align: top; text-decoration: underline;}

.btn-tooltip-trigger{ position: relative; display: inline-block; height: 19px; color: #666; padding-top: 1px; padding-right: 19px; vertical-align: top; font-size: 12px; line-height: 18px; letter-spacing: -0.3px;}
.btn-tooltip-trigger:after { content:''; position: absolute; top: 0; right: 0; display: block; width: 19px; height: 19px; background: url(../images/ui/ui_bic_tooltip.png) center / 14px no-repeat;}
.btn-tooltip-trigger.btn-spot { border: 0; background-color: transparent; }
.btn-tooltip-trigger.btn-spot:after { background-image: url(../images/ui/ui_bic_tooltip_spot.png); }

.btn-tooltip-trigger.has-text {  }
.btn-tooltip-trigger.has-text:after { background-position: center right; }

.btn-line-text{display: inline-block; padding: 2px 0; text-decoration: underline; font-size: 12px; line-height: 18px; letter-spacing: -0.3px; vertical-align: top; color: #999;}
.btn-line-text:hover, .btn-line-text:active, .btn-line-text:focus{text-decoration: underline;}

a.sub-line-link, .sub-line-link{ display: inline-block; font-size: 14px; letter-spacing: -0.35px; text-decoration: underline;}
a.sub-line-link.sm, .sub-line-link.sm{font-size: 13px; letter-spacing: -0.325px; text-decoration: underline;}
a.sub-line-link.sm .text, .sub-line-link.sm .text{font-weight: 700;}

a.sub-line-spot, .sub-line-spot { display: inline-block; font-size: 0; }
.sub-line-spot .text { display: inline-block; font-size: 13px; line-height: 14px; border-bottom: 1px solid #00b495; color: #00b495; letter-spacing: -0.325px; }
.sub-line-spot [class*=bic-] { margin-right: 5px; vertical-align: -2px; }

.btn-ic-share { width: 30px; height: 30px; background: url(../images/common/comm_bic_share.png) center / 21px no-repeat; }

.btn-text-gray { display: inline-block; font-size: 14px; line-height: 20px; letter-spacing: -0.35px; color: #999; }
.btn-ic-like { width: 30px; height: 30px; background: url(../images/common/comm_bic_like.png) center / 20px no-repeat;}
.btn-ic-like.is-active {background-image: url(../images/common/comm_bic_like_active.png); }

a.btn-lnround, .btn-lnround { padding: 13px 17px 12px; display: inline-block; border: 1px solid #666; border-radius: 50px; }
a.btn-lnround .text, .btn-lnround  .text { font-size: 15px; line-height: 18px; letter-spacing: -0.375px; font-weight: 700; font-style: italic; }

a.btn-round, .btn-round { border-radius: 50px; }


/**********************************************************************************
** (iOS Only)
***********************************************************************************/
@supports (-webkit-touch-callout: inherit) {
	a.link-black-text.link-sm:after, .link-black-text.link-sm:after { margin-top: 6px; }
}

