html, body {width: 100%; color: #000; background-color: #fff; font-size: 14px; letter-spacing: -0.35px; line-height: 1.2;}
a, button, input, select, textarea {  font-size: 13px; letter-spacing: -0.325px; line-height: 1.2; }
