/* input */
.ip-desc { display: block; margin-top: 5px; font-size: 11px; color: #999; line-height: 17px; letter-spacing: -0.28px; }

/* select */
.row-sel-wrap { display: -webkit-box; display: -ms-flexbox; display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex;  }
.row-sel-wrap .ui-select { flex: 1; }
.row-sel-wrap .ui-select ~ .ui-select { margin-left: 4px; }
.ui-select{display: inline-block; vertical-align: top; width: 100%; }

/* textarea */
.ui-textarea{display: inline-block; width: 100%; height: 225px; padding: 14px; border: 1px solid #ddd; font-size: 13px; resize: none; vertical-align: top; color: #000; line-height: 20px; letter-spacing: -0.35px; transition: border-color .23s;}
.ui-textarea:focus{border-color: #00b495;}
.ui-textarea[disabled]:focus, .ui-textarea[readonly]:focus{border-color: #ddd;}
.byte-check-wrap{display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; flex-direction: column; border: 1px solid #ddd; transition: border-color .23s;}
.byte-check-wrap.is-focus{border-color: #00b495;}
.byte-check-wrap.is-disabled{background-color: #fafafa;}
.byte-check-wrap .ui-textarea{height: 192px; border: none;}
.byte-check-wrap .ui-textarea:focus ~ .byte-check .count,
.byte-check-wrap .ui-input:focus ~ .byte-check .count{color: #00b495;}
.byte-check-wrap .ui-textarea:focus ~ .byte-check .total,
.byte-check-wrap .ui-input:focus ~ .byte-check .total{color: #999;}
.byte-check-wrap .byte-check{margin: 7px 14px 7px 0; font-size: 0; text-align: right; line-height: 17px;}
.byte-check-wrap .byte-check .count, .byte-check-wrap .byte-check .total{display: inline-block; font-size: 11px; vertical-align: top; letter-spacing: -0.28px;}
.byte-check-wrap .byte-check .count{color: #ccc; transition: color .23s;}
.byte-check-wrap .byte-check .total:before{content: '/';}
.byte-check-wrap .byte-check .total{color: #ccc;}
.byte-check-wrap.type-ip{flex-direction: row;}
.byte-check-wrap.type-ip .ui-input{flex: 1 1 auto; height: 38px; padding-right: 0; border: none;}
.byte-check-wrap.type-ip .byte-check{flex-shrink: 0; margin: 3px 15px 0; line-height: 35px;}

/* radio/check */
.ui-rdo, .ui-chk {position: relative; display: inline-block; min-height: 22px; font-size: 0; vertical-align: top;}
.ui-rdo input, .ui-chk input, .favicon-chk input {position: absolute; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip: rect(0, 0, 0, 0); border: 0;}
.ui-rdo input[disabled] + label, .ui-chk input[disabled] + label, .ui-rdo input[readonly] + label, .ui-chk input[readonly] + label {pointer-events: none;}
.ui-rdo input + label, .ui-chk input + label {display: inline-block; position: relative; min-height: 22px; padding: 1px 0 0 29px; font-size: 13px; line-height: 19px; color: #666; cursor: pointer; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; vertical-align: top; letter-spacing: -0.325px; transition: color .23s;}
.ui-rdo input:checked + label, .ui-chk input:checked + label { color: #000; }

.ui-rdo input + label:before, .ui-rdo input + label:after, .ui-chk input + label:before {content: ''; position: absolute; left: 0; top: 0; width: 22px; height: 22px; text-align: center; border-radius: 50%; background-color: #fff; border: 1px solid #ddd; -webkit-box-sizing: border-box; box-sizing: border-box;}
.ui-rdo input + label:after{width: 10px; height: 10px; top: 6px; left: 6px; background: #ddd; border: none;}
.ui-rdo input:checked + label:before, .ui-chk input:checked + label:before {border-color: #00b495;}
.ui-rdo input:checked + label:after{background: #00b495;}

.ui-rdo input + label:before, .ui-rdo input + label:after {border-radius: 50%; transition: background 0.23s, border-color 0.23s; -webkit-transition: background 0.23s, border-color 0.23s;}
.ui-rdo input[disabled] + label, .ui-rdo input[readonly] + label {color: #a3a3a3;}
.ui-rdo input[disabled] + label:before, .ui-rdo input[disabled] + label:after,
.ui-rdo input[readonly] + label:before, .ui-rdo input[readonly] + label:after {opacity: .5;}

.ui-chk input + label:before {-webkit-transition: border-color 0.23s, background-color 0.23s; transition: border-color 0.23s, background-color 0.23s;}
