.tab-wrap.tab-pay-wrap .easy-pay-list{font-size: 0;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item{display: inline-block; width: calc((100% - 4px)/2); vertical-align: top;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item:nth-child(2n){margin-left: 4px;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item:nth-child(n+3){margin-top: 6px;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item .btn-pay{display: flex; flex-direction: column; justify-content: center; align-items: center; width: 100%; height: 75px; border: 1px solid #ddd; transition: border-color .23s; background-color: #fff;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item .btn-pay img{width: 102px; height: 25px;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item .btn-pay .pay-type{margin-top: 6px; display: block; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #666; transition: color .23s;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item.is-active .btn-pay{border-color: #000;}
.tab-wrap.tab-pay-wrap .easy-pay-list .item.is-active .btn-pay .pay-type{color: #000;}

/* round line */
.tab-wrap.round-line .tabs-menu-wrap{overflow-x: auto; margin: 0 -15px 20px; padding: 0 15px; -webkit-overflow-scrolling: touch;}
.tab-wrap.round-line .tabs-menu-wrap::-webkit-scrollbar {display: none; /* Chrome, Safari, Opera*/}
.tab-wrap.round-line .tabs-menu-wrap .tabs { display: block; height: auto; min-height: auto; margin-bottom: 0; border-bottom: 0; font-size: 0; white-space: nowrap;}
.tab-wrap.round-line .tabs .tab-item { display: inline-block; }
.tab-wrap.round-line .tabs .tab-item:last-child { margin-right: 15px; }
.tab-wrap.round-line .tabs .tab-item + .tab-item { margin-left: 5px; }
.tab-wrap.round-line .tabs .tab-item .tab-link { padding: 6px 11px 5px; border: 1px solid #ccc; border-radius: 50px; }
.tab-wrap.round-line .tabs .tab-item .tab-link .tab-text {font-weight: 300; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #000;}

.tab-wrap.round-line .tabs .tab-item.ui-state-active .tab-link { border-color: #f58229; background: #f58229;}
.tab-wrap.round-line .tabs .tab-item.ui-state-active .tab-link .tab-text { font-weight: 300; color: #fff; }
.tab-wrap.round-line .tabs .tab-item.ui-state-active .tab-link:after { display: none; }

/* scroll tab */
.tab-wrap.scroll-tab .tabs-menu-wrap { overflow-x: auto; margin: 0 -15px 20px; padding: 0 15px; -webkit-overflow-scrolling: touch; }
.tab-wrap.scroll-tab .tabs-menu-wrap::-webkit-scrollbar {display: none; /* Chrome, Safari, Opera*/}
.tab-wrap.scroll-tab .tabs-menu-wrap .tabs { display: block; height: auto; min-height: auto; margin-bottom: 0; border-bottom: 0; font-size: 0; white-space: nowrap;}
.tab-wrap.scroll-tab .tabs .tab-item { display: inline-block; }
.tab-wrap.scroll-tab .tabs .tab-item:last-child { margin-right: 30px; }
.tab-wrap.scroll-tab .tabs .tab-item + .tab-item { margin-left: 6px; }
.tab-wrap.scroll-tab .tabs .tab-item .tab-link { padding: 8px 12px 7px; border: 0; border-radius: 10px; background-color: #F8F8F8; }
.tab-wrap.scroll-tab .tabs .tab-item .tab-link .tab-text { font-weight: 400; font-size: 16px; line-height: 19px; color: #6F6F6F; }
.tab-wrap.scroll-tab .tabs .tab-item.ui-state-active .tab-link { background-color: #00B490;}
.tab-wrap.scroll-tab .tabs .tab-item.ui-state-active .tab-link .tab-text { font-weight: 600; color: #fff; }

/**********************************************************************************
** table
***********************************************************************************/
/* row */
.tbl-wrap{padding-top: 14px;}
.tbl-wrap + .tbl-wrap{margin-top: 20px; border-top: 1px solid #eee;}
.tbl-wrap .tbl-row tr th{padding: 6px 0; font-size: 13px; line-height: 19px; text-align: left; letter-spacing: -0.325px; vertical-align: top;}
.tbl-wrap .tbl-row tr td{padding: 6px 0;}
.tbl-wrap .tbl-row tr:last-child td{padding-bottom: 0;}

.tbl-wrap .tbl-row tr td .tbl-text{font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #000; vertical-align: top; text-align: left;}
.tbl-wrap .tbl-row tr td .prod-name{display: block; overflow: hidden; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; max-height: 40px; font-size: 13px; line-height: 19px; letter-spacing: -0.325px;}
.tbl-wrap .tbl-row tr td .prod-name ~ .prod-name{margin-top: 4px;}

/* col */
.tbl-col-wrap{border-top: 1px solid #000;}
.tbl-col-wrap .tbl-col thead tr th{padding: 11px 5px; border-bottom: 1px solid #ddd; font-size: 12px; line-height: 18px; letter-spacing: -0.3px; color: #333;}
.tbl-col-wrap .tbl-col tbody tr td{padding: 14px 5px 9px; border-bottom: 1px solid #ddd; text-align: center;}
.tbl-col-wrap .tbl-col tbody tr td .agree-chk, .tbl-col-wrap .tbl-col tbody tr td .agree-no-chk{display: inline-block; width: 12px; height: 12px; background-position: 0; background-repeat: no-repeat; background-size: 100%; vertical-align: top;}
.tbl-col-wrap .tbl-col tbody tr td .agree-chk{background-image: url(../images/join/ic_agree_chk_on.png);}
.tbl-col-wrap .tbl-col tbody tr td .agree-no-chk{background-image: url(../images/join/ic_agree_chk_off.png);}
.tbl-col-wrap + .btn-wrap{margin-top: 40px;}

.tbl-col-wrap.has-line-lg .tbl-col thead tr th { color: #666; }
.tbl-col-wrap.has-line-lg .tbl-col tbody tr td { padding: 14px 10px 14px; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; border-right: 1px solid #ddd; }
.tbl-col-wrap.has-line-lg .tbl-col tbody tr td:last-child { border-right: 0; }

.tbl-col-wrap.type-th-gray .tbl-col thead tr th { padding-top:16px; padding-bottom:15px; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; font-weight: 500; background: #f9f9f9; }
.tbl-col-wrap.type-th-gray .tbl-col tbody tr td { padding-top:15px; padding-bottom:15px; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; word-break: break-all;}

/* 텍스트 정보성 */
.tbl-info-wrap .tbl-info tbody th, .tbl-info-wrap .tbl-info tbody td{padding: 7px 0; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; text-align: left; vertical-align: top;}
.tbl-info-wrap .tbl-info tbody tr:first-child th, .tbl-info-wrap .tbl-info tbody tr:first-child td{padding-top: 0;}
.tbl-info-wrap .tbl-info tbody tr:last-child th, .tbl-info-wrap .tbl-info tbody tr:last-child td{padding-bottom: 0;}
.tbl-info-wrap .tbl-info tbody th{padding-right: 10px;}
.tbl-info-wrap .tbl-info .interior-home-info .home-info{color: #000;}
.tbl-info-wrap .tbl-info .price{display: block; font-size: 0; line-height: 19px;}
.tbl-info-wrap .tbl-info .price .val{font-size: 15px; font-weight: 500; letter-spacing: -0.375px; display: inline-block; vertical-align: top;}
.tbl-info-wrap .tbl-info .price .unit{font-size: 11px; letter-spacing: -0.28px; display: inline-block; vertical-align: top;}
.tbl-info-wrap .no-data-desc{padding-top: 2px; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #999; text-align: center;}
.tbl-info-wrap + .info-text-list{padding-top: 20px; border-top: 1px solid #ddd;}



/**********************************************************************************
** no data
***********************************************************************************/
.no-data{display: block; padding: 100px 0 100px;text-align: center;}
.no-data .ic-nodata{margin-bottom: 15px;}
.no-data .no-data-desc{display: block; font-size: 13px; line-height: 19px; letter-spacing: -0.325px; color: #000;}
.no-data .no-data-sdesc{display: block; margin-top: 13px; font-size: 12px; line-height: 18px; letter-spacing: -0.3px; color: #666;}
.no-data .btn-wrap { margin-top: 25px; }
.no-data.type-line { border-top: 1px solid #000; }

/* contbox 마지막으로 no-data 오는 경우 */
.contbox .no-data.bottom-gap-sm{padding-bottom: 70px;}
.contbox .no-data.top-gap-sm{padding-top: 75px;}
.contbox .page-tit-wrap + .no-data:not(.type-line){padding-top: 89px;}

.contbox .no-data + .info-text-list { padding-top:16px; border-top: 1px solid #ddd; }


/**********************************************************************************
** 등록 비디오 플레이
***********************************************************************************/
.attach-box .attach-video { overflow: hidden; position: relative; text-align: center;}
.attach-box .attach-video .btn-thumbnail-play {position: relative; display: block; z-index: 1; margin: 0 auto; /*position: absolute; top: 0; left: 0; bottom: 0; width: 100%; height: 100%;*/ text-align: center;}
.attach-box .attach-video .btn-thumbnail-play:before { z-index: 2; content:''; position: absolute; top: 50%; left: 50%; width: 60px; height: 60px; background: url(../images/ui/ui_ic_play_video.png) 0 / 100% no-repeat; transform: translate(-50%, -50%);}
.attach-box .attach-video .btn-thumbnail-play:after { z-index: 1; content:''; display: block; position: absolute; top: 0; right: 0; bottom: 0; left: 0; background: #000; opacity: 0.2; }
.attach-box .attach-video .btn-thumbnail-play img {max-width: 100%; }
.attach-box .attach-video .video-content {display: none; opacity: 0; position: relative; max-width: 100%; vertical-align: top; }
.attach-box .attach-video.is-play .video-content { opacity: 1; display: block; margin: 0 auto;}
.attach-box .attach-video.is-play .btn-thumbnail-play {display: none;}
/**********************************************************************************
** 359px 이하 ( 디자인 기준(360px)보다 작은 사이즈 대응 )
***********************************************************************************/
@media (max-width: 359px) {
	.search-gray-box{padding-left: 0; padding-right: 0;}
	.search-keyword-wrap{width: 250px;}

	/* MMY_010601 > 시공정보 버튼 3개 320이하 처리 */
	.page-tit-wrap.device-sm{display: block;}
	.page-tit-wrap.device-sm .right-box{justify-content: flex-end; margin-top: 6px;}
}

/**********************************************************************************
** 280px 이하 ( 갤럭시 폴더폰 대응 ) - device-xs
***********************************************************************************/
@media (max-width: 280px) {
	.form-wrap .form-box .form-title.both .coupon-state-list li + li:before{margin: 6px 5px 0 4px;}
	.file-list li {width: calc((100% - 20px) / 3);}
	.file-list .file-item .btn-box {width: 100%;}
	.date-range-wrap .date-picker-box .date-box .ui-date{padding-right: 35px; padding-left: 10px;}
}

/**********************************************************************************
** (iOS Only)
***********************************************************************************/
@supports (-webkit-touch-callout: inherit) {
	.byte-check-wrap.type-ip .byte-check{flex-shrink: 0; margin: 0 15px; line-height: 38px;}
	.page-sm-tit .btn-tooltip-trigger {margin-top: 2px;}
	.text-guide-wrap .guide-desc .btn-tooltip-trigger {transform: translateY(2px)}
	.form-wrap .form-box .form-cont .rdo-toggle-box .ui-rdo .rdo-btn-label .btn-tooltip-trigger{transform: none; }
}

/**********************************************************************************
** 봄디톡 오픈시 body 스크롤 히든
***********************************************************************************/
body.happytalk-open {position: fixed; width: 100%; height: var(--reVh, 1vh); top: 0; left: 0; overflow-y: hidden; }
