/*html,  body {width:100%; height: calc(constant(safe-area-inset-bottom) + constant(safe-area-inset-top) + var(--reVh, 1vh) ); height: calc(env(safe-area-inset-bottom) + constant(safe-area-inset-top) + var(--reVh, 1vh) ); !*overflow-y:auto; overflow-x:hidden;*! }*/
html,  body {width:100%; height: var(--reVh, 1vh); /*overflow-y:auto; overflow-x:hidden;*/ }
body {position: relative; background: #fff; margin: 0 !important; }

.wrapper.has-docker{ padding-bottom: 60px; padding-bottom: calc(constant(safe-area-inset-bottom) + 60px); padding-bottom: calc(env(safe-area-inset-bottom) + 60px);}
.wrapper.has-docker.no-footer{padding-bottom: 115px; padding-bottom: calc(constant(safe-area-inset-bottom) + 115px); padding-bottom: calc(env(safe-area-inset-bottom) + 115px);}
.wrapper.has-docker-lg{padding-bottom: 80px; padding-bottom: calc(constant(safe-area-inset-bottom) + 80px); padding-bottom: calc(env(safe-area-inset-bottom) + 80px);}
.wrapper.has-docker-lg.no-footer{padding-bottom: 135px; padding-bottom: calc(constant(safe-area-inset-bottom) + 135px); padding-bottom: calc(env(safe-area-inset-bottom) + 135px);}
.wrapper.no-footer{padding-bottom: 55px; padding-bottom: calc(constant(safe-area-inset-bottom) + 55px); padding-bottom: calc(env(safe-area-inset-bottom) + 55px);}

/**********************************************************************************
** header
***********************************************************************************/
/* 띠배너 */
.top-banner-wrap.is-fixed{top: 0; z-index: 12; transition: top .23s;}
.top-banner-wrap {position: relative; text-align: center; height: 40px;}
.top-banner-wrap .top-banner-link{display: block; height: 100%; text-align: center;}
.top-banner-wrap img{height: 100%;}
.top-banner-wrap .btn-top-banner-close{position: absolute; top: 0; right: 0; display: block; width: 40px; height: 40px; background: url(../images/common/comm_bic_top_banner_close.png) center / 15px no-repeat;}
.top-banner-wrap:not([style*="display: none"]) ~ .header-wrapper.is-fixed {top: 40px;}

.top-banner-wrap.is-fixed.is-sticky.scroll-up{top: -40px;}
.top-banner-wrap.is-fixed.is-sticky.scroll-down{top: 0;}
.top-banner-wrap:not([style*="display: none"]).is-fixed.is-sticky.scroll-down ~ .header-wrapper.is-main.is-sticky.scroll-down{top: 40px;}
.top-banner-wrap:not([style*="display: none"]).is-fixed.is-sticky ~ .header-wrapper.is-main.is-sticky ~ .container-wrapper .gnb-menu-wrap.sps-blw{top: 90px;}
.top-banner-wrap:not([style*="display: none"]).is-fixed.is-sticky.scroll-up ~ .header-wrapper.is-main.is-sticky.scroll-up ~ .container-wrapper .gnb-menu-wrap.sps-blw{top: 0;}


/* header */
.is-fixed{ z-index: 3; position: fixed; left: 0; right: 0; }
.header-wrapper.is-fixed{position: fixed; top: 0; left: 0; height: 51px; background-color: #fff; z-index: 10; border-bottom: 1px solid #eee;}
.header-wrapper.is-fixed ~ .container-wrapper{padding-top: 51px;}
.top-banner-wrap:not([style*="display: none"]).is-fixed ~ .header-wrapper.is-fixed:not(.is-main) ~ .container-wrapper{padding-top: 91px;}
.header-wrapper{position: relative; padding: 12px 90px;}

.header-wrapper:not(.is-allmenu) > [class*='header-']:not(.header-center) > [class*='btn-'] {display: inline-block; width: 30px; height: 30px; vertical-align: top;}
.header-wrapper:not(.is-allmenu) > [class*='header-']:not(.header-center) > [class*='btn-'] + [class*='btn-']{margin-left: 12px;}
.header-wrapper:not(.is-allmenu) > [class*='header-']:not(.header-center) > [class*='btn-'] [class*='ic-']{width: 26px; height: 26px; margin: 2px;}
.header-wrapper .header-right{position: absolute; top: 10px; right: 13px; font-size: 0; z-index: 3;}
.header-wrapper .ic-header-back{background-image: url(../images/common/comm_bic_header_back.png);}
.header-wrapper .ic-header-search{background-image: url(../images/common/comm_bic_header_search.png);}
.header-wrapper .ic-header-cart{background-image: url(../images/common/comm_bic_header_cart.png);}
.header-wrapper .btn-header-cart{position: relative;}
.header-wrapper .btn-header-cart .count{position: absolute; top: 3px; right: 2px; display: block; min-width: 13px; height: 14px; padding: 0 3px; border-radius: 7px; background-color: #f58229; color: #fff; font-size: 9px; text-align: center; line-height: 14px;}

.header-wrapper .header-left{position: absolute; top: 10px; left: 13px; font-size: 0; z-index: 3;}
.header-wrapper .header-center{position: relative; overflow: hidden; text-align: center; z-index: 3;}
.header-wrapper .cate-dropdown-wrap{opacity: 0; transition: opacity .23s, top .3s; transform-origin: left top; position: fixed; top: 50px; left: 0; width: 100%; background: #fff; z-index: 2; max-height: 455px; overflow-y: auto; -webkit-overflow-scrolling: touch; border-bottom: 1px solid #ddd;}
.header-wrapper .cate-dropdown-wrap .cate-list{padding: 10px 15px; text-align: center;}
.header-wrapper .cate-dropdown-wrap .cate-list .list-item{padding: 11px 0;}
.header-wrapper .cate-dropdown-wrap .cate-list .list-item a{font-size: 14px; font-weight: 500; color: #999; letter-spacing: -0.35px; line-height: 21px;}
.header-wrapper .cate-dropdown-wrap .cate-list .list-item.is-active a{color: #000; text-decoration: underline;}
.header-wrapper.is-open{z-index: 11;}
.header-wrapper.is-open:before{content:''; position: absolute; top: 0; left: 0; width: 100%; height: 50px; background: #fff; z-index: 3;}
.header-wrapper.is-open .cate-dropdown-wrap{top: 50px; max-height: 455px; opacity: 1; overflow-y: auto; -webkit-overflow-scrolling: touch;}
.header-wrapper.is-hide .cate-dropdown-wrap{top: -100% !important;}
.top-banner-wrap.is-fixed ~ .header-wrapper .cate-dropdown-wrap,
.top-banner-wrap.is-fixed ~ .header-wrapper.is-open .cate-dropdown-wrap{top: 90px;}

.header-wrapper .page-tit{overflow: hidden; font-size: 18px; font-weight: 500; letter-spacing: -0.45px; white-space: nowrap; text-overflow: ellipsis; line-height: 26px;}
.header-wrapper .page-tit .btn-cate-dropdown{max-width: 100%; overflow: hidden; display: inline-flex; font-size: 0; vertical-align: top;}
.header-wrapper .page-tit .btn-cate-dropdown .text{overflow: hidden; font-size: 18px; font-weight: 500; letter-spacing: -0.45px; white-space: nowrap; text-overflow: ellipsis; line-height: 26px; color: #000;}
.header-wrapper .page-tit .btn-cate-dropdown .ic-cate-more{flex-shrink: 0; width: 12px; height: 12px; margin: 7px 0 0 6px; background: url(../images/common/comm_bic_header_cate_arr.png) 0 / 100% no-repeat; transform: rotate(0); transition: transform .23s;}
.header-wrapper.is-open .header-center .btn-cate-dropdown .ic-cate-more{transform: rotate(180deg);}

/**********************************************************************************
** container
***********************************************************************************/
.container-wrapper{overflow-x: hidden; background-color: #f9f9f9;}
.container-wrapper .container-body{overflow: hidden; position: relative;}
.container-wrapper .container-body .contbox:not([style*="display: none"])  ~ .contbox{margin-top: 0px;}
.container-wrapper .container-body .contbox{background-color: #fff;}
.container-wrapper .container-body .contbox .contbox-body{padding: 25px 0 30px;}
.wrapper.no-footer .container-wrapper .container-body .contbox:last-child .contbox-body{padding-bottom: 0;}
.container-wrapper .container-body .contbox .contbox-body-inner{padding: 0 15px;}
.container-wrapper .container-body .contbox.sm .contbox-body{padding: 15px 0;}
.container-wrapper .container-body .contbox.sm .contbox-body .contbox-body-inner{display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;}
.container-wrapper .container-body .contbox.sm .contbox-body .contbox-body-inner .right-box{margin-left: 20px;}
.container-wrapper .container-body .contbox.no-top-gap .contbox-body{padding-top: 0;}
.container-wrapper .container-body .contbox.no-bottom-gap .contbox-body{padding-bottom: 0;}

.container-wrapper .sub-cate-wrap{position: relative; background-color: #fff;}
.container-wrapper .sub-cate-wrap .cate-list-wrap{ z-index: 8; position: relative; height: 41px; border-bottom: 1px solid #eee; padding-right: 10px; background: #fff; }
.container-wrapper .sub-cate-wrap .cate-list-wrap .swiper-wrapper{padding: 0 15px;}
.container-wrapper .sub-cate-wrap .cate-list-wrap .swiper-slide{display: block; width: auto; margin-right: 20px;}
.container-wrapper .sub-cate-wrap .cate-list-wrap .swiper-slide a{display: block; line-height: 40px; font-size: 13px; font-weight: 500; color: #999; letter-spacing: -0.65px;}
.container-wrapper .sub-cate-wrap .cate-list-wrap .swiper-slide.is-active a{border-bottom: 1.5px solid #00B495; color: #000;}
.container-wrapper .sub-cate-wrap .btn-cate-all{opacity: 1; transform: scale(0); z-index: 9; position: absolute; top: 0; right: 0; width: 70px; height: 40px; background-color: #fff; transition: opacity .13s ease-in-out; text-align: right; background: -webkit-gradient(linear, left top, right top, color-stop(0, rgba(255, 255, 255, 0)), color-stop(0.3, #fff), color-stop(1, #fff)); background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #fff 30%, #fff 100%); background: -webkit-linear-gradient(left,  rgba(255, 255, 255, 0) 0%, #fff 30%, #fff 100%); background: -o-linear-gradient(left,  rgba(255, 255, 255, 0) 0%, #fff 30%, #fff 100%); background: -ms-linear-gradient(left,  rgba(255, 255, 255, 0) 0%, #fff 30%, #fff 100%); background: linear-gradient(left,  rgba(255, 255, 255, 0) 0%, #fff 30%, #fff 100%);}
.container-wrapper .sub-cate-wrap .btn-cate-all:after{content: ''; display: inline-block; width: 15px; height: 15px; margin-right: 19px; background: url(../images/common/comm_bic_sub_cate_arr.png) 0 / 100% no-repeat; transform: rotate(0); transition: transform .23s; vertical-align: top;}

.container-wrapper .sub-cate-wrap .sub-cate-all{opacity: 0; position: absolute; top: -167px; left: 0; width: 100%; padding: 6px 0 15px; background: #fff; z-index: 7; transition: opacity .23s, top .23s; transform-origin: top left; border-bottom: 1px solid #ddd;}
.container-wrapper .sub-cate-wrap .sub-cate-all .swiper-container{height: 100%;}
.container-wrapper .sub-cate-wrap .sub-cate-all .swiper-container.is-active{padding-bottom: 14px;}
.container-wrapper .sub-cate-wrap .sub-cate-all .category-sub-list{display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; flex-direction: column; flex-wrap: wrap; height: 90px; padding-left: 15px;}
.container-wrapper .sub-cate-wrap .sub-cate-all .category-sub-list .cate-item a{display: inline-block; max-width: 100%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; vertical-align: top; line-height: 30px; font-size: 12px; color: #999; letter-spacing: -0.3px;}
.container-wrapper .sub-cate-wrap .sub-cate-all .category-sub-list .cate-item{width: 50%; padding-right: 15px; line-height: 30px;}
.container-wrapper .sub-cate-wrap .sub-cate-all .category-sub-list .cate-item.is-active a{color: #000; font-weight: 500;}
.container-wrapper .sub-cate-wrap.is-more .btn-cate-all{opacity: 1; transform: scale(1);}
.container-wrapper .sub-cate-wrap.is-more .cate-list-wrap .swiper-wrapper{padding-right: 70px !important;}
.container-wrapper .sub-cate-wrap.is-more .cate-list-wrap{padding-right: 65px;}
.container-wrapper .sub-cate-wrap.is-more.is-active .sub-cate-all{opacity: 1; top: 41px; }
.container-wrapper .sub-cate-wrap.is-more.is-active .btn-cate-all:after{transform: rotate(180deg);}

/**********************************************************************************
** footer
***********************************************************************************/
.container-footer{ position: relative; z-index: 5; /*margin-top: 10px;*/ padding: 40px 15px 65px; background-color: #fff;}
.container-footer:after { content:''; position: absolute; top: 0; left: 0; right: 0; height: 10px; display: block; background: #f9f9f9; }
/* .container-footer .footer-ci{display: block; width: 140px; height: 15px; margin-bottom: 18px; background: url(../images/common/comm_footer_logo.png) 0 / 100% no-repeat;} */
.container-footer .footer-ci{display: block; width: 140px; height: 15px; margin-bottom: 18px; background: url("../images/common/footer-ci--renew.svg") 0 / 100% no-repeat;}
.container-footer .fnb-list{max-width: 290px;}
.container-footer .fnb-list .fnb-item{display: inline-block; margin-top: 10px; vertical-align: top;}
.container-footer .fnb-list .fnb-item:not(:last-child):after{content: ''; display: inline-block; width: 1px; height: 9px; margin: 3px 7px 0 10px; background-color: #ddd; vertical-align: top; line-height: 1;}
.container-footer .fnb-list .fnb-item .fnb-link{font-size: 13px; color: #999;}
.container-footer .fnb-list .fnb-item.fc-black .fnb-link{font-weight: 500; color: #000;}
.container-footer .footer-info-wrap .ft-sns-list { margin-top: 27px; font-size: 0; }
.container-footer .footer-info-wrap .ft-sns-list .item {display: inline-block; width: 30px; height: 30px; margin-right: 6px; vertical-align: top; }
.container-footer .footer-info-wrap .ft-sns-list .item:last-child {margin-right: 0; }
.container-footer .footer-info-wrap .ft-sns-list .item .fsns {display: block; width: 100%; height: 100%; background-repeat: no-repeat; background-size: 100%; background-position: 0; }
.container-footer .footer-info-wrap .ft-sns-list .item .fsns.instagram {background-image: url(../images/common/comm_ic_sns_instagram.png); }
.container-footer .footer-info-wrap .ft-sns-list .item .fsns.youtube {background-image: url(../images/common/comm_ic_sns_youtube.png); }
.container-footer .footer-info-wrap .ft-sns-list .item .fsns.blog {background-image: url(../images/common/comm_ic_sns_blog.png); }
.container-footer .footer-info-wrap .ft-sns-list .item .fsns.facebook {background-image: url(../images/common/comm_ic_sns_facebook.png); }

.container-footer .footer-info-wrap .cs-info-wrap{margin-top: 24px;}
.container-footer .footer-info-wrap .cs-info-wrap .btn-cs{line-height: 1; font-size: 20px; font-weight: 500; letter-spacing: -0.7px;}
.container-footer .footer-info-wrap .cs-info-wrap .cs-detail{display: block; margin-top: 7px; font-size: 13px; letter-spacing: -0.325px;}
.container-footer .footer-info-wrap .ft-info-wrap{margin-top: 25px;}
.container-footer .footer-info-wrap .ft-info-wrap .ft-info-tit{font-size: 12px; color: #000; letter-spacing: -0.3px;}
.container-footer .footer-info-wrap .ft-info-wrap .ft-info-txt{font-size: 12px; line-height: 21px; color: #666; letter-spacing: -0.3px;}
.container-footer .footer-info-wrap .ft-info-wrap .ft-info-tit .btn-flink,
.container-footer .footer-info-wrap .ft-info-wrap .ft-info-txt .btn-flink{display: inline-block; min-width: 78px; height: 21px; margin: -4px 0 0 5px; border: 1px solid #ddd; line-height: 19px; font-size: 11px; color: #999; letter-spacing: -0.28px; text-align: center; vertical-align: top;}
.container-footer .footer-info-wrap .ft-info-wrap .ft-info-txt + .ft-info-txt{margin-top: 1px;}
.container-footer .footer-info-wrap .ft-info-wrap .ft-info-tit + .ft-info-txt{margin-top: 3px;}
.container-footer .footer-info-wrap .ft-copy-wrap{padding-top: 16px; margin-top: 20px; border-top: 1px solid #eee; font-size: 11px; line-height: 17px; letter-spacing: -0.28px; color: #999;}

/**********************************************************************************
** docker
***********************************************************************************/
.docker-wrapper{position: fixed; left: 0; bottom: 0; display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; width: 100%; height: 60px; height: calc(constant(safe-area-inset-bottom) + 60px); height: calc(env(safe-area-inset-bottom) + 60px); z-index: 10; background-color: #fff;}
.docker-wrapper .docker-list{flex: 1; font-size: 0; height: 60px; padding: 0 7px 0 3px; background-color: #fff; border-top: 1px solid #eee;}
.docker-wrapper .docker-list .docker-item{display: inline-block; width: 25%; height: 100%; vertical-align: top;}
.docker-wrapper .docker-list .docker-item .btn-docker{display: block; padding-top: 9px; width: 100%; height: 100%; text-align: center;}
.docker-wrapper .docker-list .docker-item [class*='ic-docker']{display: block; width: 26px; height: 26px; margin: 0 auto;}
.docker-wrapper .docker-list .docker-item .ic-docker-cate{background-image: url(../images/common/comm_bic_docker_cate.png);}
.docker-wrapper .docker-list .docker-item .ic-docker-home{background-image: url(../images/common/comm_bic_docker_home.png);}
.docker-wrapper .docker-list .docker-item .ic-docker-mypage{background-image: url(../images/common/comm_bic_docker_mypage.png);}
.docker-wrapper .docker-list .docker-item .ic-docker-estimate{background-image: url(../images/common/comm_bic_docker_estimate.png);}
.docker-wrapper .docker-list .docker-item .text{display: block; margin-top: 6px; font-size: 10px; letter-spacing: -0.25px; color: #000; line-height: 10px; text-align: center;}
.docker-wrapper .btn-docker-floating{ display: none; position: relative; flex-shrink: 0; width: 70px; height: 70px; background-color: #00b495; text-align: center; box-shadow: -9px 4px 20px 0 rgba(0, 152, 89, 0.2); transform: translateY(-10px); text-align: center; }
.docker-wrapper .btn-docker-floating .ic-vhomd-guide{ margin-top: 1px; }
.docker-wrapper .btn-docker-floating .text{display: block; margin-top: 1px; font-size: 10px; font-weight: 700; color: #fff; letter-spacing: -0.25px; line-height: 17px; text-align: center; }
.docker-wrapper .btn-docker-floating .text .eng { letter-spacing: 0; font-weight: 700; color :#fff; }
.docker-wrapper .btn-docker-floating .ic-pop-close-white { margin: 5px 0 0; }
.docker-wrapper .btn-docker-floating.is-active { display: block; }

.docker-wrapper .floating-guide-box { position: absolute; bottom: 84px; right: 15px; padding: 14px 0 13px; width: 225px; background: #fff; box-shadow: 0 10px 20px 0 rgba(153, 153, 153, 0.2); text-align: center; border-radius: 16px; }
.docker-wrapper .floating-guide-box:after { content:''; position: absolute; bottom: -3px; right: 15px; display: block; width: 10px; height: 11px; background: #fff; transform: rotateZ(-45deg) skew(-15deg, -15deg); border-radius: 2px; }
.docker-wrapper .floating-guide-box .guide-desc { color: #666; font-size: 12px; line-height: 18px; letter-spacing: -0.3px; }
.docker-wrapper .floating-guide-box .guide-desc .fc-spot { font-weight: 700; }
.docker-wrapper .floating-guide-box .guide-desc .fc-black { font-weight: 500; }


.btn-go-top{opacity: 0; position: fixed; bottom: 25px; bottom: calc(constant(safe-area-inset-bottom) + 25px); bottom: calc(env(safe-area-inset-bottom) + 25px); right: 20px; width: 40px; height: 40px; padding: 14px 15px 14px 14px; background: #fff; border: 1px solid #eee; border-radius: 100%; box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.08); transition: opacity .23s; z-index: 10;}
.btn-go-top:before{content: ''; display: block; width: 10px; height: 10px; background: url(../images/common/comm_ic_gotop.png) 0 / 100% no-repeat; }
.btn-go-top.is-hide{opacity: 0; right: -40px;}
.btn-go-top.is-active{opacity: 1; right: 20px;}

.wrapper.has-docker .btn-go-top{bottom: 85px; bottom: calc(constant(safe-area-inset-bottom) + 85px); bottom: calc(env(safe-area-inset-bottom) + 85px);}
.wrapper.has-docker-lg .btn-go-top{bottom: 105px; bottom: calc(constant(safe-area-inset-bottom) + 105px); bottom: calc(env(safe-area-inset-bottom) + 105px);}

.docker-wrapper.type-ord{display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex;}
.docker-wrapper.type-ord .btn-lg{flex: 1; height: 100%; height: calc(constant(safe-area-inset-bottom) + 100%); height: calc(env(safe-area-inset-bottom) + 100%); padding: 19px 10px 18px; font-weight: 300; font-size: 14px; line-height: 20px; letter-spacing: -0.35px; align-items: flex-start;}
.docker-wrapper.type-ord .btn-lg .val{padding-right: 7px; color: #fff;}
.docker-wrapper.type-ord .btn-lg .text{font-weight: 700; color: #fff;}
.wrapper.has-docker.no-footer .btn-go-top{bottom: 75px; bottom: calc(constant(safe-area-inset-bottom) + 75px); bottom: calc(env(safe-area-inset-bottom) + 75px);}
.wrapper.has-docker-lg.no-footer .btn-go-top{bottom: 95px; bottom: calc(constant(safe-area-inset-bottom) + 95px); bottom: calc(env(safe-area-inset-bottom) + 95px);}
.wrapper.no-footer .btn-go-top{bottom: 15px; bottom: calc(constant(safe-area-inset-bottom) + 15px); bottom: calc(env(safe-area-inset-bottom) + 15px);}

.docker-wrapper.type-prod{display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex;}
.docker-wrapper.type-prod .btn-lg,
.docker-wrapper.type-prod .sold-out{flex: 1; height: 100%; height: calc(constant(safe-area-inset-bottom) + 100%); height: calc(env(safe-area-inset-bottom) + 100%); padding: 19px 10px 18px; font-weight: 300; font-size: 14px; line-height: 20px; letter-spacing: -0.35px; align-items: flex-start;}
.docker-wrapper.type-prod .sold-out{display: inline-flex; justify-content: center; border: 1px solid #00b495; background-color: #00b495; opacity: .3; color: #fff; font-weight: 700;  }

.docker-wrapper.type-prod .btn-color-opt { z-index: 1; position: relative; display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; justify-content: center; width: 100%; padding: 19px 20px 0; border: 0; border-top: 1px solid #ddd; text-align: center; }
.docker-wrapper.type-prod .btn-color-opt .opt-color { flex: 0 0 17px; display: inline-block; width: 17px; height: 17px; margin-right: 10px; border-radius: 17px; margin-top: 2px; border: 1px solid #ddd; vertical-align: top; }
.docker-wrapper.type-prod .btn-color-opt .text { display: inline-block; font-size: 14px; line-height: 20px; letter-spacing: -0.35px; vertical-align: top; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
.docker-wrapper.type-prod .btn-color-opt .ic-more-arr { margin: 5px 0 0 10px; transform: rotate(180deg); }

.docker-wrapper.type-estimate{align-items: flex-start; display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; height: 80px; height: calc(constant(safe-area-inset-bottom) + 80px); height: calc(env(safe-area-inset-bottom) + 80px); padding: 15px; box-shadow: 0 -2px 20px 0 rgba(0, 0, 0, 0.08);}
.docker-wrapper.type-estimate .compare-box{display: flex; width: 100%;}
.docker-wrapper.type-estimate .compare-box.after{display: none;}
.docker-wrapper.type-estimate .compare-box .btn-lg{flex: 1;}
.docker-wrapper.type-estimate.is-compare .compare-box.before{display: none;}
.docker-wrapper.type-estimate.is-compare .compare-box.after{display: flex;}
.docker-wrapper.type-estimate .compare-box.after .btn-reset-text{width: 90px; height: 50px; margin-left: 0; flex: 0 1 auto;}
