/**********************************************************************************
** search
***********************************************************************************/
body.is-search{overflow: hidden;}
body.is-search .wrapper{height: var(--reVh, 1vh); overflow: hidden;}
body.is-search .wrapper .docker-wrapper{display: none;}
body.is-search .wrapper .btn-go-top{display: none;}
.header-wrapper.is-search{display: flex; height: 52px; padding: 10px 13px; border-bottom: 2px solid #000; z-index: 11; transition: border-bottom-color .23s;}
.header-wrapper.is-search:after{content: ''; position: absolute; top: 52px; left: 0; display: block; width: 100%; height: 20px; transition: background .23s; background: -webkit-gradient(linear, left top, left bottom, color-stop(1, rgba(0, 0, 0, 0))); background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0) 100%); background: -webkit-linear-gradient(top,  rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0) 100%); background: -o-linear-gradient(top,  rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0) 100%); background: -ms-linear-gradient(top,  rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0) 100%); background: linear-gradient(top,  rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0) 100%); filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='rgba(0, 0, 0, 0.03)', endColorstr='rgba(0, 0, 0, 0)', gradientType=0);}
.header-wrapper.is-search.is-focus{border-bottom-color: #00b495;}
.header-wrapper.is-search.is-focus:after{background: -webkit-gradient(linear, left top, left bottom, color-stop(1, rgba(0, 142, 84, 0))); background: -moz-linear-gradient(top, rgba(0, 142, 84, 0.05) 0%, rgba(0, 142, 84, 0) 100%); background: -webkit-linear-gradient(top,  rgba(0, 142, 84, 0.05) 0%, rgba(0, 142, 84, 0) 100%); background: -o-linear-gradient(top,  rgba(0, 142, 84, 0.05) 0%, rgba(0, 142, 84, 0) 100%); background: -ms-linear-gradient(top,  rgba(0, 142, 84, 0.05) 0%, rgba(0, 142, 84, 0) 100%); background: linear-gradient(top,  rgba(0, 142, 84, 0.05) 0%, rgba(0, 142, 84, 0) 100%); filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='rgba(0, 142, 84, 0.05)', endColorstr='rgba(0, 142, 84, 0)', gradientType=0);}

.header-wrapper.is-search.is-fixed ~ .container-wrapper{padding-top: 52px;}
.header-wrapper.is-search > [class*='btn-'] {display: inline-block; width: 30px; height: 30px; vertical-align: top;}
.header-wrapper.is-search > [class*='btn-'] + [class*='btn-']{margin-left: 12px;}
.header-wrapper.is-search > [class*='btn-'] [class*='ic-']{width: 26px; height: 26px; margin: 2px;}


