.header-wrapper.is-main{/*background-color: transparent; height: 50px; transition: background-color .23s;*/ border-bottom: none;}
.header-wrapper.is-main ~ .container-wrapper{padding-top: 0;}
.top-banner-wrap:not([style*="display: none"]) ~ .header-wrapper.is-main ~ .container-wrapper{padding-top: 40px;}
.renewal-top-banner-wrap:not([style*="display: none"]) ~ .header-wrapper.is-main ~ .container-wrapper {
	padding-top: 68px;
}
.header-wrapper.is-sub-main,
.header-wrapper.is-main{padding: 0 15px;}
.header-wrapper .logo{height: 50px; width: 81px;}
.header-wrapper .logo a.btn-logo{display: block; width: 100%; height: 100%; background: url(../images/common/comm_header_logo.png) 0 / 100% no-repeat; transition: background-image .23s;}
/*.header-wrapper.is-main .ic-header-cart{background-image: url(../images/common/comm_bic_header_cart_white.png); transition: background-image .23s;}
.header-wrapper.is-main .ic-header-search{background-image: url(../images/common/comm_bic_header_search_white.png); transition: background-image .23s;}*/
.header-wrapper.is-main.is-sticky{background-color: #fff; transition: top .23s;}
.header-wrapper.is-main.is-sticky .logo a.btn-logo{background-image: url(../images/common/comm_header_logo.png); }
.header-wrapper.is-main.is-sticky .ic-header-cart{background-image: url(../images/common/comm_bic_header_cart.png); }
.header-wrapper.is-main.is-sticky .ic-header-search{background-image: url(../images/common/comm_bic_header_search.png); }
.header-wrapper.is-main.is-sticky.scroll-up{top: -50px;}

.header-wrapper.is-main.is-sticky.scroll-up ~ .container-wrapper .gnb-menu-wrap.sps.sps-blw{top: 0;}
.header-wrapper.is-main.is-sticky.scroll-down{top: 0;}
.header-wrapper.is-main.is-sticky.scroll-down ~ .container-wrapper .gnb-menu-wrap.sps.sps-blw{top: 40px;}
.top-banner-wrap:not([style*="display: none"]) ~ .header-wrapper.is-main.is-sticky.scroll-down ~ .container-wrapper .gnb-menu-wrap.sps.sps-blw{top: 90px;}

.gnb-menu-wrap{z-index: 105; overflow-x: auto; height: 40px; background-color: #fff; white-space: nowrap; -webkit-overflow-scrolling: touch; -ms-overflow-style: none; /* IE and Edge */ scrollbar-width: none; /* Firefox */ }
.gnb-menu-wrap.sps { opacity: 0; height: 0; transition: opacity .23s, top .23s; }
.gnb-menu-wrap.sps.sps-blw{opacity: 1; top: 50px; height: 40px;}
.gnb-menu-wrap::-webkit-scrollbar {display: none; /* Chrome, Safari, Opera*/}
.gnb-menu-wrap .gnb-menu{height: 100%; font-size: 0; background: #fff; }
.gnb-menu-wrap .gnb-menu .menu-item{position: relative; display: inline-block; height: 100%; margin: 0 4px; vertical-align: top;}
.gnb-menu-wrap .gnb-menu .menu-item:after{content: ''; position: absolute; left: 0; bottom: 0; display: block; width: 100%; height: 2px; background-color: #fff; transition: background-color .23s;}
.gnb-menu-wrap .gnb-menu .menu-item:first-child{margin-left: 15px;}
.gnb-menu-wrap .gnb-menu .menu-item:last-child{margin-right: 15px;}
.gnb-menu-wrap .gnb-menu .menu-item .depth1-tit{display: block; padding: 0 8px; font-size: 14px; letter-spacing: -0.35px; line-height: 40px;}
.gnb-menu-wrap .gnb-menu .menu-item.is-active:after{background-color: #00b495}
.gnb-menu-wrap .gnb-menu .menu-item.is-active .depth1-tit{font-weight: 700;}

.top-banner-wrap:not([style*="display: none"]) ~ .header-wrapper.is-main ~ .container-wrapper .gnb-menu-wrap.sps.sps-blw{top: 90px;}
.top-banner-wrap:not([style*="display: none"]) ~ .header-wrapper.is-main.is-fixed ~ .container-wrapper .gnb-menu-wrap.sps.sps-blw{top: 90px;}


