@font-face {
	font-family: 'BlueberrySans';
	font-style: normal;
	font-weight: 700;
	font-display: swap;
	src:
		url('font/BlueberrySans-Bold.woff2') format('woff2'),
		url('font/BlueberrySans-Bold.woff') format('woff'),
		url('font/BlueberrySans-Bold.otf') format('truetype');
}


@font-face {
	font-family: 'BlueberrySans';
	font-style: normal;
	font-weight: 500;
	font-display: swap;
	src:
		url('font/BlueberrySans-Medium.woff2') format('woff2'),
		url('font/BlueberrySans-Medium.woff') format('woff'),
		url('font/BlueberrySans-Medium.otf') format('truetype');
}


@font-face {
	font-family: 'BlueberrySans';
	font-style: normal;
	font-weight: 300;
	font-display: swap;
	src:
		url('font/BlueberrySans-Regular.woff2') format('woff2'),
		url('font/BlueberrySans-Regular.woff') format('woff'),
		url('font/BlueberrySans-Regular.otf') format('truetype');
}
