/**********************************************************************************
** 359px 이하 ( 디자인 기준(360px)보다 작은 사이즈 대응 )
***********************************************************************************/
@media (max-width: 359px) {
	.prod-form-wrap.cart .prod-form-list .item .btn-wrap{flex-wrap: wrap;}
	.prod-form-wrap.cart .prod-form-list .item .btn-wrap .btn-sm:first-child{flex-basis: 100%; margin-bottom: 5px;}
	.prod-form-wrap.cart .prod-form-list .item .btn-wrap [class*=btn] + [class*=btn]{margin-left: 4px;}
	.prod-form-wrap.cart .prod-form-list .item .btn-wrap .btn-sm:nth-child(2){margin-left: 0;}

	.fold-box-wrap.faq .fold-box .question-title:before, .fold-box-wrap.faq .fold-box .answer-desc:before{top: -1px;}
	.fold-box-wrap.faq .fold-box .answer-desc:before{top: 0;}
	.list-sort-header.individual .ui-row-list .item { margin-right: 10px; }

	.ord-state-box-wrap .ord-state-list{text-align: center;}
	.ord-state-box-wrap .ord-state-list .ord-state-item,
	.ord-state-box-wrap .ord-state-list.col4 .ord-state-item{width: calc(100% / 3);}
	.ord-state-box-wrap .ord-state-list .ord-state-item:nth-child(n+4){margin-top: 16px;}

	.mypage-main .user-ord-wrap .claim-box-wrap .claim-list .claim-item{flex-wrap: wrap; justify-content: center;}
	.mypage-main .user-ord-wrap .claim-box-wrap .claim-list .claim-item .count-val{width: 100%; text-align: center;}

	.consulting-intro-wrap .consulting-list .item .rdo-item input + label br{display: none;}
}

/**********************************************************************************
** 280px 이하 ( 갤럭시 폴더폰 대응 ) - device-xs
***********************************************************************************/
@media (max-width: 280px) {
	.login-wrap .sns-login-wrap .sns-list .item{margin-right: 7px;}
	.prod-form-wrap .form-header .ui-rdo.type-lg .ord-num{padding-left: 0;}
	.list-sort-header.individual .ui-row-list .item { margin-right: 7px; }
	.list-sort-header.individual .ui-row-list .ui-rdo input + label,
	.list-sort-header.individual .ui-row-list .ui-chk input + label { padding-left: 25px; font-size: 12px; letter-spacing: -0.3px; }
	.prod-form-wrap .form-header .btn-text-gray { margin-left: 6px; padding-left: 7px; }

	.coupon-item .coupon-cont .coupon-info{display: block;}
	.coupon-item .coupon-cont .coupon-info .detail-text{margin-top: 5px;}

	.result-bottom{text-align: left;}
}

/**********************************************************************************
** (iOS Only)
***********************************************************************************/
@supports (-webkit-touch-callout: inherit) {
	.form-control-list{transform: none;}
	.event-list-wrap .event-list .list-item .event-info .info-footer .event-range{margin-top: 0;}
	.board-list-wrap .board-list .item .link-text{margin-top: 1px;}
	.prod-form-wrap .prod-form-list .item .review-info .review-item + .review-item:before{margin-top: 5px;}
	.payment-info-wrap .payment-box .payment-tit .btn-tooltip-trigger {margin-top: 0;}
	.point-info-wrap .point-info .info-item .date {margin-top: -2px;}
	.point-info-wrap .point-info .info-item .date:before {margin-top: 4px;}
}

