@charset "utf-8";

/* reset */
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,textarea,button{margin:0;padding:0}
fieldset,img{border:0 none}
dl,ul,ol,menu,li {list-style:none}
blockquote, q {quotes:none}
blockquote:before, blockquote:after,q:before, q:after {content:'';content:none}
input,select,textarea,button {font-size:100%;vertical-align:middle}
button {border:0 none;background-color:transparent;cursor:pointer}
table {border-collapse:collapse;border-spacing:0}
body{-webkit-text-size-adjust:none} input[type='text'],input[type='password'],input[type='submit'],input[type='search'] {-webkit-appearance:none; border-radius:0}
input:checked[type='checkbox'] {background-color:#666; -webkit-appearance:checkbox}
button,input[type='button'],input[type='submit'],input[type='reset'],input[type='file'] {-webkit-appearance:button; border-radius:0}
body {min-width:320px;background-color:#fff}
body,th,td,input,select,textarea,button {font-size:14px;line-height:1.5;font-family:'Malgun Gothic', '맑은 고딕', sans-serif;color:#333}

a {color:#333;text-decoration:none} a:active, a:hover {text-decoration:none}
address,caption,cite,code,dfn,em,var {font-style:normal;font-weight:normal}

.show {display:block}
.hide {display:none}
.a-header { padding: 2px;background-color: brown; margin: 10px;text-align:center;color:#ffffff}
.b-header { padding: 2px;background-color: blue; margin: 10px;text-align:center;color:#d9d9d9}
.top-header {padding:2px 0;font-size:13px;background-color:#00b495;text-align:center;color:#ffffff}
.main-header { padding: 2px;background-color: red; margin: 10px;text-align:center;color:#000000}
.custom-body { padding: 2px;background-color: #e3d3d3; margin: 10px;text-align:center;color: #000000;min-height: 50vh}
.normal-span {width: 10px; height: 50px; background-color: #00b495; }
.floating-box {width: 50px;background-color: #04cf5c; height: 50px;margin: 10px;position: fixed;bottom:20px;}

.footer {padding:10px 0;border-top:1px solid #d9d9d9;font-size:11px;font-size:12px;background-color:#f1f1f1;text-align:center;color:#888}
.gnb {overflow:hidden;background-color:#F6F6F6}
.gnb li {float:left;width:33.3%}
.gnb li:nth-child(2) {width:33.4%}
.gnb .tab_item {display:block;padding:6px 0;text-align:center;color:#000000}
.gnb .tab_item.on {background-color:#999; color:#ffffff}

